@import url('https://fonts.googleapis.com/css?family=Poppins:200i,400&display=swap');

$color1: #eaabfa;
$color2: #fad7a5;

body {
    background: rgb(115, 115, 196);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Poppins", sans-serif;
    font-size: 1.5rem;
    background-color: $color2;
    background-image: linear-gradient(
            45deg,
            $color1 25%,
            transparent 25%,
            transparent 75%,
            $color1 75%,
            $color1
        ),
        linear-gradient(
            -45deg,
            $color1 25%,
            transparent 25%,
            transparent 75%,
            $color1 75%,
            $color1
        );
    background-size: 60px 60px;
    background-position: 0 0;
    animation: slide 8s infinite linear;
}

@keyframes slide {
    from {
        background-position: 0 0;
    }

    to {
        background-position: -120px 60px;
    }
}

.modal {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3rem 5rem;
    border: 3px solid black;
    border-radius: 5px;
    background: white;
    box-shadow: 8px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 0.25rem;
}
h1 {
    font-weight: 400;
    line-height: 1.2;
}

p {
    font-size: 1.5rem;
}
h1,
p {
    margin-top: 0;
    margin-bottom: 0.5rem;
}
label {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

input,
button,
select,
textarea {
    margin: 0;
    font-family: inherit;
    font-size: 1rem;
    line-height: inherit;
}

button {
    font-family: inherit;
    font-size: inherit;
    background: white;
    padding: 0.3rem 3.4rem;
    border: 3px solid black;
    margin-right: 2.6rem;
    box-shadow: 6px 6px 0 0 black;
    transition: all 0.2s;

    &:hover {
        box-shadow: 0.4rem 0.4rem 0 black;
        transform: translate(-0.4rem, -0.4rem);
    }
    &:active {
        box-shadow: 0 0 0 black;
        transform: translate(0, 0);
    }
}

.form-group {
    margin: 0 auto 1.25rem auto;
    padding: 0.25rem;
}

.form-control {
    display: block;
    width: 100%;
    padding: .2rem .5rem;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.input-textarea {
    min-height: 120px;
    width: 100%;
    padding: 0.625rem;
    resize: vertical;
  }
  
  .submit-button {
    width: 100%;
    cursor: pointer;
  }
  